.profile {
  width: 600px;
  border-radius: 10px;

  margin: 0 auto;
  
  background-color: #ffffff;
  box-shadow: 0px 1px 1px rgba(61, 59, 59, 0.12), 0px 4px 4px rgba(70, 67, 67, 0.06),
    1px 4px 6px rgba(56, 53, 53, 0.16);
}
.description {
  padding: 15px 0;
  text-align: center;

  box-shadow: 0px 1px 1px rgb(0 0 0 / 12%), 0px 4px 4px rgb(0 0 0 / 6%),
    1px 4px 6px rgb(0 0 0 / 16%);
  }
.avatar {
  width: 150px;
    
  margin: 0 auto;
}
.name {
  font-weight: 700;
  font-size: 25px;

  margin-bottom: 10px;
}
.tag {
  color: gray;

  margin-bottom: 10px;
}
.location {
 color: gray;

  margin-bottom: 10px;
}
.stats {
  display: flex;
  text-align: center;

  background-color: #b6c3ce;
}

.item {
  width: 33.3%;

  padding: 20px;
}
.item:not(:last-child) {
  border-right: 1px solid #9a9aa5;
}
.label {
  display: block;

  margin-bottom: 10px;

  color: gray;
}
.quantity {
  font-weight: 700;

  font-size: 20px;
}